.feed{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-column-gap: 16px;
    grid-row-gap: 30px;
    margin-top: 15px;
}

.card{
    box-shadow: 0 2px 4px grey;
}

.card img{
    width: 100%;
    border-radius: 5px;
}

.card h2{
    font-size: 16px;
    line-height: 20px;
    font-weight: 400;
    color: #000;
    margin: 5px 7px 5px 7px;
    overflow-x: hidden;
}

.card h3{
    font-size: 14px;
    font-weight: 600;
    color: #555;
    margin:  5px 7px 5px 7px;
}


.card p{
    font-size: 14px;
    margin:  5px 7px 5px 7px;
}