.sidebar{
    background: #fff;
    width: 15%;
    height: 100vh;
    position: fixed;
    top: 0;
    padding-left: 2%;
    padding-top: 80px;
    overflow-y:auto;
    scrollbar-width: none;
    transition: 0.5s;
}


.shortcut-links img{
    width: 20px;
    margin-right: 20px;
}

.side-link{
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    width: auto;
    flex-wrap: wrap;
    cursor: pointer;
    background:#ccc;
    padding: 10px;
    border-radius: 5%;
    font-size: medium;
}

.side-link:hover{
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    width: auto;
    flex-wrap: wrap;
    cursor: pointer;
    background:crimson;
    color: #fff;
    padding: 10px;
    border-radius: 5%;
}

.sidebar hr{
    border: 0;
    height: 1px;
    background: #ccc;
    width: 85%;
}

.subscribed-list h3{
    font-size: 13px;
    margin: 20px 0;
    color: #5a5a5a;
}

.subscribed-list img{
    width: 25px;
    border-radius: 50%;
    margin-right: 20px;
}

.small-sidebar{
    width: 5%;
}

.small-sidebar p{
    display: none;
}

.small-sidebar h3{
    display: none;
}

.small-sidebar hr{
    width: 50%;
    margin-bottom: 25px;
}

.side-link.active img{
    padding-bottom: 2px;
    border-bottom: 3px solid red;
}

.small-sidebar .button{
    display: none;
}

.button{
    background: red;
    border: none;
    font-size: 15px;
    color: white;
    padding: 10px;
    margin-top: 20px;
    margin-left: 30px;
    z-index: 100;

}

@media (max-width:900px) {
    .sidebar{
        display: none;
    }
    .small-sidebar{
        display: block;
        width: 50px;
        margin-left: 2px;
        background: transparent;
    }
}